html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

address {
  font-style: normal;
}

/* глобальный сброс маржинов */
ul {
  margin: 0;
  padding: 0;
  list-style: none;
}
/* глобальный сброс подчеркивания ссылок */
a {
  text-decoration: none;
  color: inherit;
}

body {
  margin: 0px;
  padding: 0px;
  font-family: DM Sans, sans-serif;

  font-size: 14px;
  line-height: 1.76;
  letter-spacing: 0.04em;
  background-image: linear-gradient(
    to bottom,
    #ffb8ca 20%,
    #ffffff 20%,
    #ffffff 40%,
    #fff5f6 40%,
    #fff5f6 60%,
    #ffffff 60%,
    #ffffff 80%,
    #fff5f6 80%,
    #fff5f6 100%
  );

  background-repeat: no-repeat;
}

/* Делает картинки блочными */
img {
  display: block;
  width: 100%;
  height: auto;
}

/* Сброс типографии */
h1,
h2,
h3,
h4,
h5,
h6,
p {
  margin-top: 0;
  margin-bottom: 0;
}

.animation {
  transition-duration: 250ms;
  transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
}

.hidden {
  position: absolute;
  clip: rect(1px 1px 1px 1px);
  clip: rect(1px, 1px, 1px, 1px);
  padding: 0;
  border: 0;
  height: 1px;
  width: 1px;
  overflow: hidden;
}

.container {
  margin: 0 auto;
  padding: 0 15px;
  @media screen and (max-width: 767px) {
    min-width: 320px;
    max-width: 480px;
  }
  @media screen and (min-width: 768px) and (max-width: 1279px) {
    width: 768px;
  }
  @media screen and (min-width: 1280px) {
    width: 1280px;
  }
}
.btn {
  align-items: center;
  font-family: inherit;
  font-size: 16px;
  font-weight: 700;
  line-height: 1.31;
  padding-top: 15px;
  padding-bottom: 15px;
  border-radius: 22px;
  border-style: none;
  cursor: pointer;
  background-color: $main-text-color;
}
